import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';


@Component({
    selector: 'custom-eval-select',
    templateUrl: './custom-evaluate-select.html',
    styleUrls: ['./custom-evaluate-select.scss']
})

export class CustomEvalSelectComponpoent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    @Input() inVal: any;

    //公共
    tableMesg: any = tableMessage;
    msgs: Message[] = [];
    // 控件
    selectedValues: any[] = [];//单选数组
    allVal: any = true;//全选值
    selectedAllVal: any = [];//全选数组
    flag: boolean = false;//中间量
    userId: string = '';
    userName: string = '';
    first: number = 0;
    pageNum: number = 1;
    pageSize: number = 10;
    totalNum: number;
    tableList: any[] = [];

    constructor(
        public httpService: CusOperationHttpService,
    ) {

    }
    ngOnInit() {
        this.selectGroupAdminDetailInfo();
    }
    select() {
        //选择评价人
        this.outValue.emit({ custGroupListDisplay: false, roleList: this.selectedValues });
        this.selectedValues = [];//单选数组
        this.allVal = true;//全选值
        this.selectedAllVal = [];//全选数组
        this.flag = false;//中间量
    }
    checkBinary(event) {
        //  单选
        if (event && this.selectedValues.length == this.tableList.length && !this.flag) {
            this.changeSelect(true, 0);
        }
        else if (this.flag && !event) {
            this.changeSelect(false, 0);
        }
    }
    changeSelect(event, checkOne) {
        // 全选
        if (event) {
            let temArr = [];
            let temArr2 = _.clone(this.tableList);
            for (let i in temArr2) {
                temArr.push(temArr2[i]);
            }
            this.selectedValues = temArr;
            this.flag = true;
            this.selectedAllVal = []
            this.selectedAllVal.push(true)
        }
        else {
            this.flag = false;
            this.selectedAllVal = []
            this.selectedAllVal.push(false)
            if (checkOne == 0) {
                // 单选点击

            }
            else if (checkOne == 1) {
                // 全选点击   
                let temArr = [];
                this.selectedValues = temArr;
            }
        }
    }

    selectGroupAdminDetailInfo(flag?) {
        if (flag == 'init') {
            this.pageNum = 1;
            this.pageSize = 10;
            this.first = 0;
        }
        let params = {
            partyClassificationGroupId: this.inVal,
            statusId: "1",
            userId: this.userId,
            userName: this.userName,
            pageNum: this.pageNum,
            pageSize: this.pageSize,
        }
        this.httpService.selectGroupAdminDetailInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data.resultList;
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功' }];
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }

    paginate(event) {
        this.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        this.selectGroupAdminDetailInfo();
    }
}