import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service'
import { GroupEvalInfoBean, CustGroupListBean } from './../../bean/custom-group.bean';
import { Observable } from 'rxjs/Rx';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import * as _ from 'lodash'
import { ActivatedRoute, Params, Router } from '@angular/router';
import { GroupAdminInfoBean } from './bean/GroupAdminInfo.bean';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";


@Component({
  selector: 'app-appraiser-evaluate',
  templateUrl: './appraiser-evaluate.component.html',
  styleUrls: ['./appraiser-evaluate.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class AppraiserEvaluateComponent implements OnInit {
  list_1: any;
  //公共
  tableMesg: any = tableMessage;
  // 标题
  appraiserEvaluate: string = "评价人评价";
  appraiser: string = "评价人";
  evaluateInformation: string = "评价信息";
  arr: any;
  cars: any;
  groupEvalInfoBean: GroupEvalInfoBean = new GroupEvalInfoBean(); //第三方评价
  groupAdminInfoBean: GroupAdminInfoBean = new GroupAdminInfoBean();//客户群管理人员
  custGroupListBean: CustGroupListBean = new CustGroupListBean();//客户群名单
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOAD;
  msgs: Message[] = [];
  // 中间量
  sysCreditEvalOut: string;//评价人客户号中间量
  isChange: boolean = false;
  // 控件
  evalName: SelectItem[];//评价人名称
  evalNameSel: string = '';//评价人选择绑定
  items: any[] = [];
  // 模态框传参
  custGroupListDisplay: boolean = false;//模态框隐现
  inVal: any = [];
  data: any = {};
  // 模态框
  saveDisplay: boolean = false;

  //
  files: any = [];
  fileList: any = [];//上传文件列表
  // UPLOAD: string = TZB_CUS_UPLOAD;
  // 模态框
  batchDisplay: boolean = false;//批量导入
  addDisplay: boolean = false;//返回结果
  first: any = 0;


  // 链接传参
  valTemp1: any = '';//客户名单传来字符
  valTemp2: any = '';//客户名单传来字符待分割数组
  groupId: any = '';//群号
  groupName: any = '';//群号
  custGroupView: any = '';//群号
  customInfoFlag: any = 0;//显示返回按钮
  customInfoData: any = '';//单客户传参
  // 中间量
  isSave: boolean = false;
  // 码值
  code: any;
  groupHealthFlag: any;// 健康状况
  groupCreditEval: any;// 信用评价
  groupSourceOfIncome: any;// 收入来源
  groupFamilyStableFlag: any;// 家庭关系是否稳定
  groupBadHabits: any;// 不良嗜好
  groupCarFlag: any;// 车辆状况
  groupIncomeLevel: any; // 家庭年收入级别
  groupOfferLines: any;// 建议额度
  title2: string = '群评价人批量导入';
  fileNameFlag: boolean = true;//

  constructor(
    public httpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.codeValues();
    this.groupHealthFlag = this.code['GroupHealthFlag'];
    this.groupCreditEval = this.code['GroupCreditEval'];
    this.groupSourceOfIncome = this.code['GroupSourceOfIncome'];
    this.groupFamilyStableFlag = this.code['GroupFamilyStableFlags'];
    this.groupBadHabits = this.code['GroupBadHabits'];
    this.groupCarFlag = this.code['GroupCarFlag'];
    this.groupIncomeLevel = this.code['GroupIncomeLevel'];
    this.groupOfferLines = this.code['GroupOfferLinese'];
    //初始化list
    setTimeout(() => {
      this.list_1 = [];
    });
    //
    this.preSet();
    this.selectGroupAdminInfo();// 客户群管理人员
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  preSet() {
    // // 假数据
    // this.groupId = '360001'
    // let tempArr = [
    //   {
    //     partyId: '1',
    //     partyName: '2'
    //   }
    // ]
    // this.groupEvalInfoBean.evalList = _.clone(tempArr);
    // 初始化
    // 链接传值
    if (this.routerInfo.snapshot.queryParams['cgId']) {
      this.groupId = this.routerInfo.snapshot.queryParams['cgId'];
    }

    if (this.routerInfo.snapshot.params['cId']) {
      this.valTemp1 = this.routerInfo.snapshot.params['cId'];
    }
    if (this.routerInfo.snapshot.params['cName']) {
      this.valTemp2 = this.routerInfo.snapshot.params['cName'];
    }


    //判断
    let valTemp1Arr = [];
    let valTemp2Arr = [];
    // 客户号
    let flag = this.valTemp1.split(',')
    if (this.valTemp1 == '') {
      valTemp1Arr = [];
    }
    else if (this.valTemp1.length == 1) {
      valTemp1Arr.push(this.valTemp1);
    }
    else {
      valTemp1Arr = this.valTemp1.split(',');
    }
    // 客户名
    if (this.valTemp2 == '') {
      valTemp2Arr = [];
    }
    else if (this.valTemp2.length == 1) {
      valTemp2Arr = [this.valTemp2];
    }
    else {
      valTemp2Arr = this.valTemp2.split(',');
    }
    // 组合
    let evalListArr = [];
    if (valTemp1Arr.length > 0) {
      for (let i in valTemp1Arr) {
        evalListArr.push({ partyId: valTemp1Arr[i], partyName: valTemp2Arr[i] })
      }
      this.groupEvalInfoBean.evalList = evalListArr;
    }
    // 其他链接传参--主要
    if (this.routerInfo.snapshot.params['id']) {
      this.groupId = this.routerInfo.snapshot.params['id'];
    }
    if (this.routerInfo.snapshot.params['groupId']) {
      this.groupId = this.routerInfo.snapshot.params['groupId'];
    }
    if (this.routerInfo.snapshot.params['groupName']) {
      this.groupName = this.routerInfo.snapshot.params['groupName'];
      this.appraiser = this.groupName + '-' + this.appraiser;
      this.evaluateInformation = this.groupName + '-' + this.evaluateInformation;
    }
    if (this.routerInfo.snapshot.params.customInfoFlag) {
      this.customInfoFlag = this.routerInfo.snapshot.params.customInfoFlag;
    }
    if (this.routerInfo.snapshot.params.data) {
      this.customInfoData = this.routerInfo.snapshot.params.data;
    }
    this.groupEvalInfoBean.groupId = this.groupId;
    this.groupEvalInfoBean.handleFlag = '1';
    this.groupEvalInfoBean.sysCreditEval = '';
    this.custGroupListBean.groupId = this.groupId;//名单

    // this.groupEvalInfoBean.evalList = [];
    // let temp = [{ evalName: '1',incomeLevel:'rer' }, { evalName: '2' }, { evalName: '3' }]//假数据  
    // this.groupEvalInfoBean.evalList = _.clone(temp);

  }
  roleIdChange() {
    //评价人联动评价人客户号
    this.saveDisplay = true;
    this.list_1 = [];
    // this.sysCreditEvalOut = this.evalNameSel;
    // this.msgs = [{ severity: 'info', summary: '确定', detail: '你保存了评价人信息' }];
    // this.isChange = false;

    // this.evalNameSel = this.sysCreditEvalOut;
    // this.msgs = [{ severity: 'info', summary: '取消', detail: '取消' }];
    // this.isChange = false;
  }
  saveBtn() {
    this.sysCreditEvalOut = this.evalNameSel;
    this.groupEvalInfoBean.evalList = [];
    this.msgs = [];
    this.msgs = [{ severity: 'info', summary: '确定', detail: '您更改了评价人信息' }];
    this.saveDisplay = false;
  }
  cancelBtn() {
    this.evalNameSel = this.sysCreditEvalOut;
    this.msgs = [];
    this.msgs = [{ severity: 'info', summary: '取消', detail: '取消' }];
    this.saveDisplay = false;
  }
  onHid() {
    // 关闭确定模态框--
    this.evalNameSel = this.sysCreditEvalOut;
  }
  selectGroupAdminInfo() {
    // 正式使用或用bean
    this.groupAdminInfoBean.groupId = this.groupId;//群号
    this.groupAdminInfoBean.roleType = '4';//角色类型，评价人
    this.groupAdminInfoBean.statusId = '1';//状态

    this.httpService.selectGroupAdminInfo(this.groupAdminInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // 客户群管理人员
        this.evalName = [];
        for (let i in data.roleList) {
          this.evalName.push({ label: data.roleList[i].userName, value: data.roleList[i].userId });
        }
        if (data.roleList) {
          this.evalNameSel = data.roleList[0].userId;
        }
        if (data.roleList) {
          this.sysCreditEvalOut = data.roleList[0].userId;
        }
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  submitGroupEvalInfo() {
    // 提交,客户群第三方评价

    if (this.groupEvalInfoBean.evalList.length != 0) {
      let flag1 = true;
      flag1 = this.isNull();//非空校验
      if (!flag1) {
        return;
      }
    }
    this.confirmationService.confirm({
      message: '确定提交?',
      accept: () => {
        // this.evalNameSel = '2'//假数据
        this.groupEvalInfoBean.sysCreditEval = _.clone(this.evalNameSel);
        // for (let i in this.groupEvalInfoBean.evalList) {
        //   this.groupEvalInfoBean.evalList[i].sysCreditEval = _.clone(this.evalNameSel);
        // }
        let tempName = '';
        this.evalName.forEach(item => {
          if (item.value == this.evalNameSel) {
            tempName = item.label;
          }
        })
        this.groupEvalInfoBean.evalList.forEach(item => {
          item.sysCreditEval = _.clone(this.evalNameSel);
          item.evalName = _.clone(tempName);
        })
        //
        let parames = {
          groupId: this.groupId,
          handleFlag: "1",
          sysCreditEval: this.evalNameSel,
          evalList: this.list_1
        }
        //
        this.httpService.addGroupEvalInfo(parames).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
            this.selectCustGroupList(0);
          }
          else {
            this.msgs = [];
            data.returnCode.message ? data.returnCode.message : '提交失败！';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败!' }];
        })
      }
    });
  }
  add() {
    // this.confirmationService.confirm({
    //   message: '页面内容若未提交，确定新增覆盖吗?',
    //   accept: () => {
    // this.selectCustGroupList(1);
    // }
    // });
    this.custGroupListDisplay = true;
  }
  selectCustGroupList(i) {
    // 客户群名单查询 
    // 客户群名单查询
    this.first = 0;
    this.custGroupListBean.groupId = this.groupId;
    this.custGroupListBean.groupName = this.groupName;
    this.custGroupListBean.pageNum = 1;
    this.custGroupListBean.pageSize = 10;
    this.custGroupListBean["statusId"] = "1";
    let params = {
      groupId: this.groupId,
      pageNum: this.custGroupListBean.pageNum,
      pageSize: this.custGroupListBean.pageSize,
      statusId: 1
    }
    this.httpService.selectCustGroupList(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.custGroupListBean = data;
        this.custGroupListBean.custList = data.custList;
        this.inVal = this.custGroupListBean.custList;//客户名单异步传入
        this.data = this.custGroupListBean;//客户名单异步传入
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    });
  }
  closeCustGroupList(event) {
    // 关闭客户群名单查询模态框,顺便传回客户名。
    console.log(event)
    this.custGroupListDisplay = event.custGroupListDisplay;
    let tempArr = event.params;
    let returnFlag = false;
    if (tempArr) {
      let stu = tempArr.every(item => {
        let stu_2 = this.groupEvalInfoBean.evalList.every(item_2 => {
          return item_2.partyId != item.custNo;
        });
        return stu_2;
      })
      let stu_9 = tempArr.every(item => {
        return item.custNo != this.evalNameSel;
      });
      if (stu_9) {
        if (stu) {
          tempArr.forEach(item => {
            this.groupEvalInfoBean.evalList.push(item);
            this.groupEvalInfoBean.evalList[this.groupEvalInfoBean.evalList.length - 1]["partyId"] = item.custNo;
            this.groupEvalInfoBean.evalList[this.groupEvalInfoBean.evalList.length - 1]["partyName"] = item.custName;
          })
          this.groupEvalInfoBean.evalList = _.clone(this.groupEvalInfoBean.evalList)
          //
          let evalName = '';
          this.evalName.forEach(element => {
            if (this.evalNameSel == element.value) {
              evalName = element.label;
            }
          });
          //
          event.params.forEach(element => {
            this.list_1.push({
              partyId: element.custNo,
              partyName: element.custName,
              sysCreditEval: this.evalNameSel,
              evalName: evalName,
              healthFlag: '',
              creditEval: '',
              badHabits: '',
              familyStableFlag: '',
              sourceOfIncome: '',
              incomeLevel: '',
              carFlag: '',
              offerLines: '',
            })
          });
          this.list_1 = _.clone(this.list_1)
          //
        } else {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '已有客户号不能重复添加' }];
        }
      } else {

        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '不能选择与当前评价人相同客户！' }];
      }

    }
  }
  delGroupEvalInfo(index) {
    // 删除行
    this.confirmationService.confirm({
      message: '确定删除?',
      accept: () => {
        let arrTemp = _.clone(this.list_1);
        arrTemp.splice(index, 1);
        this.list_1 = arrTemp;

        this.groupEvalInfoBean.evalList.splice(index, 1);
      }
    });
  }

  // 链接
  returnCustGroupView() {
    this.confirmationService.confirm({
      message: '确定返回客户群视图?',
      accept: () => {
        let groupId = this.commfunc.compileStr(this.groupId);
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: groupId, groupName: this.groupName }]);
      }
    });
  }
  returnCustInfo() {
    this.confirmationService.confirm({
      message: '确定返回单客户页面?',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-information-display', { groupId: this.groupId, groupName: this.groupName, data: this.customInfoData }]);
      }
    });
  }

  //
  // --模态框相关
  btnBatchImport() {
    // 客户群名单批量导入触发
    this.batchDisplay = true;
  }
  closebatch() {
    // 客户群名单批量导入关闭
    this.batchDisplay = false;
  }
  returnCustGroupList() {
    // 客户群名单返回结果模态框
    this.addDisplay = false;
  }

  load(event) {
    // 上传点击,需跨域

    if (event.xhr.response) {
      this.fileList = JSON.parse(event.xhr.response);
      if (this.fileList.length > 1) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '不支持上传多个文件！' }];
        return;
      }
      if (this.fileList.hasOwnProperty('errorMsg')) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
        return;
      }

      this.custGroupListBean.filePath = this.fileList[0].fileUrl;
      this.custGroupListBean.fileName = this.fileList[0].fileName;
    }
    else {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
    }

    let param = this.custGroupListBean
    this.httpService.batchImportGroupEvalList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.custGroupListBean.successNum = data.successNum;
        this.custGroupListBean.failNum = data.failNum;
        this.custGroupListBean.failInfoList = data.failInfoList;
        this.addDisplay = true;
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '导入失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }


  // 其他
  isNull() {
    // 非空校验
    if (!this.evalNameSel || this.evalNameSel == '') {
      // 评价人名称
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '评价人名称不能为空！' }];
      return false;
    }
    let flag = true;
    this.list_1.forEach(item => {
      if (!item.healthFlag || item.healthFlag == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '健康状况不能为空！' }];
        flag = false;
        return;
      }
      if (!item.creditEval || item.creditEval == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '信用评价不能为空！' }];
        flag = false;
        return;
      }
      if (!item.badHabits || item.badHabits == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '不良嗜好不能为空！' }];
        flag = false;
        return;
      }
      if (!item.familyStableFlag || item.familyStableFlag == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '家庭关系状况不能为空！' }];
        flag = false;
        return;
      }
      if (!item.offerLines || item.offerLines == '') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '建议额度不能为空！' }];
        flag = false;
        return;
      }
    })
    if (!flag) {
      return false;
    }
    return true;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
