import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Message, SelectItem } from 'primeng/primeng';
import * as _ from 'lodash';
import { CustGroupListBean } from './../../../bean/custom-group.bean';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';


@Component({
    selector: 'app-eval-custGroupList',
    templateUrl: './appraiser-evaluation-custGroupList.html',
    styleUrls: ['./appraiser-evaluation-custGroupList.scss'],
    providers: [Commfunc]
})

export class AppEvalCustGroupListComponent implements OnInit {
    @Output() event: EventEmitter<any> = new EventEmitter<any>()
    @Input() inVal: any;
    @Input() data: any;
    @Input() groupId: any;//群号
    @Input() groupName: any;//群名称
    first: any = 0;
    //注：第三方评价客户维度和评价人维度都用到这个组件2017.11.20 
    //公共
    tableMesg: any = tableMessage;
    totalNum: any;
    pageSize: any = 1;
    pageNum: any = 10;
    list: any;//列表
    // 控件
    msgs: Message[] = [];
    allVal: any = true;//全选值
    selectedAllVal: any = [];
    selectedValues: any = [];//单选数组
    //传值
    cId: any = [];
    cName: any = [];
    params: any = {};
    // 码值
    relType: any = [];//关联关系
    idenType: any = [];//证件类型
    // 中间量
    returnDisplay: boolean = false;
    returnFlag: boolean = false;
    // bean 
    //获取所有码值
    code: any;
    custGroupListBean: CustGroupListBean = new CustGroupListBean();//客户群名单
    custNo: string;
    custName: string;

    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc

    ) {
        this.codeValues();
        this.relType = this.code['CustomGroupRelType'];
        this.idenType = this.code['CustomGroupIdenType']
    }
    ngOnInit() {
        this.selectCustGroupList();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    // 判断是否临时客户
    isQl(col) {
        if (col.custNo.search(/^QL/)) {
            return true;
        }
        return false;
    }
    // 单选
    checkBinary(event, i) {
        // debugger
        // 判断是否有效客户
        let inValTemp = [];
        this.custGroupListBean.custList.forEach(item => {
            if (item.custNo.search(/^QL/)) {
                inValTemp.push(item);
            }
        })

        // 赋值
        let arrTemp = [];
        let selectedValues = [];//局部变量
        this.selectedValues.forEach(element1 => {
            inValTemp.forEach(element2 => {
                if (element1 == element2.custNo) {
                    arrTemp.push(element2);
                }
            });
        });
        selectedValues = arrTemp;
        console.log(selectedValues);

        if (event && selectedValues.length == inValTemp.length) {//全选
            this.changeSelect(true, 0, selectedValues);
        } else {//单选
            this.changeSelect(false, 0, selectedValues);
        }
    }

    // 全选
    changeSelect(event, is_html, selectedValues) {
        // debugger
        if (event) {
            if (is_html == 0) {
                this.selectedAllVal = [true];
            }
            else if (is_html == 1) {//页面点击
                let inValTemp = [];
                this.custGroupListBean.custList.forEach(item => {
                    if (item.custNo.search(/^QL/)) {
                        inValTemp.push(item);
                    }
                })

                let arrTemp = [];
                inValTemp.forEach(element => {
                    arrTemp.push(element.custNo);
                });
                this.selectedValues = arrTemp;
            }
        }
        else {//单选
            this.selectedAllVal = [false];
            if (is_html == 0) {

            }
            else if (is_html == 1) {//页面点击
                let temArr = [];
                this.selectedValues = temArr;
            }
        }
    }

    //查询
    query(){
        this.custGroupListBean.pageNum = 1;
        this.custGroupListBean.pageSize = 10;
        this.first = 0;
        this.selectCustGroupList();
    }

    //重置
    reset() {
        this.custNo = '';
        this.custName = '';
        this.custGroupListBean.pageNum = 1;
        this.custGroupListBean.pageSize = 10;
        this.first = 0;
        this.selectCustGroupList();
    }

    //确定
    returnValue() {
        // debugger
        console.log(this.selectedValues);
        // 判断是否有效客户
        let inValTemp = [];
        this.custGroupListBean.custList.forEach(item => {
            if (item.custNo.search(/^QL/)) {
                inValTemp.push(item);
            }
        })

        // 赋值
        let arrTemp = [];
        let selectedValues = [];//局部变量
        this.selectedValues.forEach(element1 => {
            inValTemp.forEach(element2 => {
                if (element1 == element2.custNo) {
                    arrTemp.push(element2);
                }
            });
        });
        selectedValues = arrTemp;
        if (this.selectedValues && this.selectedValues.length > 0) {
            let stu = selectedValues.some(element => {
                if (element.custNo == '' || element.custNo == null || element.relType != 1) {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号不能为空且关联关系必须为本人！' }];
                    return true;
                }
            });
            if (!stu) {
                this.event.emit({ custGroupListDisplay: false, params: selectedValues });
                this.msgs = [];
                this.allVal = true;//全选值
                this.selectedAllVal = [];
                this.selectedValues = [];//单选数组
                //传值
                this.cId = [];
                this.cName = [];
                this.params = {};
                this.reset();
            }
        } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '第三方评价必须选中一个客户！' }];
            return false;
        }
    }

    //返回
    returnNow() {
        this.event.emit({ custGroupListDisplay: false });

        this.allVal = true;//全选值
        this.selectedAllVal = [];
        this.selectedValues = [];//单选数组
        //传值
        this.cId = [];
        this.cName = [];
        this.params = {}
    }

    // 客户群名单查询 
    selectCustGroupList() {
        let params = {
            groupId: this.groupId,
            listType: 1,
            statusId: "1",
            custNo: this.custNo,
            custName: this.custName,
            pageNum: this.custGroupListBean.pageNum,
            pageSize: this.custGroupListBean.pageSize
        }
        this.httpService.selectCustGroupList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.custGroupListBean.totalNum = data.totalNum;
                this.selectedAllVal = [];
                this.selectedValues = [];
                this.custGroupListBean = data;
                this.custGroupListBean.custList = data.custList;
                this.list = data.custList;
                this.list.forEach((item, i) => {
                    if (this.list[i].targetCustFlag && this.list[i].targetCustFlag != '') {
                        this.list[i].targetCustFlag == 0 ? '否' : '是';
                    }
                });

                this.list = _.clone(this.list);
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        });
    }
    paginate(event) {
        // 分页
        // this.custGroupListBean.pageNum = event.page + 1;
        // this.custGroupListBean.pageSize = event.rows;
        this.custGroupListBean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.custGroupListBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.custGroupListBean.pageSize;
        this.selectCustGroupList();//客户群名单查询
    }
}