import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ThirdPartyEvaluationComponent } from "./third-party-evaluation.component";
import { AppraiserEvaluateComponent } from "./appraiser-evaluate/appraiser-evaluate.component";
import { CustomEvaluateComponent } from "./custom-evaluate/custom-evaluate.component";
//客户群组
export const routes: Routes = [
      {
        path: '', 
        component: ThirdPartyEvaluationComponent,//创建
        children: [
          {
            path: 'appraiser-evaluate', component: AppraiserEvaluateComponent
          },
          {
            path: 'custom-evaluate', component: CustomEvaluateComponent
          },
        ]
      }
    ]

export const ThirdPartyEvaluationRouting: ModuleWithProviders = RouterModule.forChild(routes);
